*{
  margin: auto;
}

/* HEADER */
header{
  position: fixed;
  top: 0;
  left: 0;
  background-color: #333;
  z-index: 1000;
  display: flex;
  width: 100%;
  height: 4em;
  justify-content: center;
  align-items: center;
  background-color:black;
  box-shadow: 0px 5px 10px white;
}
header h1{
  position: absolute;
  left: 0;
  text-shadow: -1px -1px 0 #fff,1px -1px #fff,-1px 1px #fff, 1px 1px 0 #fff;
}
nav{
  text-align: center;
  border: 1px solid white;
  border-radius:12px;

}
nav ul{
  list-style-type: none;
  display: flex;
  margin: 0;
  padding: 0;
}
nav ul li{
  margin: 0 15px;
}
a{
  color: #fff;
  font-size: 1.2em;
  text-decoration: none;
}
nav ul li a {
  color: white;
  text-decoration: none;
  padding: 10px;
  transition: background-color 0.3s ease; 
}

nav ul li a:hover {
  background-color: #555;
}

nav ul li a.active {
  background-color: blue;
  color: white;
}

.wlk{
  display: block;
}
/*HOME*/
.home{
  width: 100%;
  height:max-content;
  display: flex;
  background:linear-gradient(to bottom, white,black);
  background: url('./component/images/bg.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
}
 .home .left{
  position: relative;
  width: 50%;
  display:block;
  align-items: center;
  margin-top: 20px;
  font-size: 18px;
  color:antiquewhite;
  text-shadow: 5px 5px 10px black;
}
.left h1{
  margin-left: 40px;
}
.left h4{
  padding-left: 100px;
  margin-top: 10px;
  font-size: 20px;
  text-decoration: underline;
}
.display {
  margin-left: 100px;
  font-size: 1.2rem;
  margin-top: 10px;
  border: 1px solid white;
  display: inline-block;
  border-radius: 4px;
  color: black;
  background:wheat;
  font-weight: bolder;
  transition: opacity 0.5s ease-in-out;
}

.display.hidden {
  opacity: 0;
}
.left h3{
  width: 40%;
  margin-top: 30px;
  margin-left: 40px;
  text-align: center;
  font-family:cursive;
  border-radius: 8px;
  font-size: 1.2em;
  border: 1px solid black;
  box-shadow: 2px 2px 10px white;
}
.contact {
  font-family: Arial, sans-serif;
  margin: 20px;
  padding: 10px;
  display: flex;
}

.email-section, .contact-section {
  margin-top: 15px;
}

.contact p {
  font-size: 23px;
  font-weight: bold;
  text-shadow: 2px 2px 3px white;
  color: black;
  margin: 0;
  padding-bottom: 5px;
}

.contact h1 {
  border: 1px solid black;
  border-radius: 7px;
  text-decoration: none;
  box-shadow: 5px 4px 1px black;
  font-size: 18px;
  margin: 0;
  line-height: 1.2;
}

.home .right{
  color:black;
  font-size: 3.5em;
  text-shadow: 13px 14px 20px white;
  text-align: center;
  border-radius: 50%;
  border: 10px solid black;
}
.right h4{
  right: 0;
  color: wheat;
  text-shadow: 10px 10px 20px white;
}